const settle = require('../core/settle');
const Cancel = require('../cancel/Cancel');
/**
 *  XMLHttpRequest
 * @param config
 * @return {Promise<unknown>}
 */
module.exports = function xhrAdapter(config) {
  return new Promise((resolve, reject) => {
    const requestData = config.data;
    const requestHeaders = config.headers;

    // 
    let onCanceled;

    // 
    function done() {
      if (config.cancelToken) {
        config.cancelToken.unsubscribe(onCanceled);
      }
    }

    let request = new XMLHttpRequest();

    // 
    request.open(config.method.toUpperCase(), config.url, true);

    // 
    request.onreadystatechange = function () {
      // readyState === 4
      if (!request || request.readyState !== 4) {
        return;
      }

      //  XMLHttpRequest  onerror 
      // file:// 0 
      if (request.status === 0 && !(request.responseURL && request.responseURL.indexOf('file:') === 0)) {
        return;
      }

      setTimeout(function onloadend() {
        if (!request) return;
        // 
        const responseHeaders = request.getAllResponseHeaders();
        // 
        const responseData = request.response;
        const response = {
          data: responseData,
          status: request.status,
          statusText: request.statusText,
          headers: responseHeaders,
          config: config,
          request: request
        };

        // 
        settle(
          (value) => {
            resolve(value);
            done();
          },
          (err) => {
            reject(err);
            done();
          },
          response
        );
      });
    };

    // 
    if ('setRequestHeader' in request) {
      Object.keys(requestHeaders).forEach((key) => {
        if (typeof requestData === 'undefined' && key.toLowerCase() === 'content-type') {
          delete requestHeaders[key];
        } else {
          request.setRequestHeader(key, requestHeaders[key]);
        }
      });
    }

    // 
    request.onabort = function () {
      if (!request) return;

      const error = new Error('Request aborted');
      error.code = 'ECONNABORTED';
      reject(error);

      request = null;
    };

    // 
    request.onerror = function () {
      reject(new Error('Network Error'));
      request = null;
    };

    // 
    if (config.cancelToken) {
      onCanceled = function (cancel) {
        reject(cancel || new Cancel('canceled'));
        request.abort();
        request = null;
      };

      config.cancelToken.subscribe(onCanceled);
    }

    request.send(requestData);
  });
};
