const Cancel = require('./cancel');

function CancelToken(executor) {
  let resolvePromise;

  //  promise
  //  promise  resolve  executor
  this.promise = new Promise((resolve) => {
    resolvePromise = resolve;
  });

  const token = this;

  this.promise.then((cancel) => {
    if (!token._listeners) return;

    for (let i = 0; i < token._listeners.length; i++) {
      token._listeners[i](cancel);
    }
    token._listeners = null;
  });

  executor(function cancel(message) {
    if (token.reason) {
      return;
    }

    token.reason = new Cancel(message);
    resolvePromise(token.reason);
  });
}

/**
 * 
 * @param listener
 */
CancelToken.prototype.subscribe = function (listener) {
  // this.reason
  // 
  if (this.reason) {
    listener(this.reason);
    return;
  }

  //  this._listeners 
  if (this._listeners) {
    this._listeners.push(listener);
  } else {
    this._listeners = [listener];
  }
};

/**
 * 
 * @param listener
 */
CancelToken.prototype.unsubscribe = function (listener) {
  if (!this._listeners) return;

  const index = this._listeners.indexOf(listener);
  if (index !== -1) {
  }
  this._listeners.splice(index, 1);
};

/**
 * 
 */
CancelToken.prototype.throwIfRequest = function () {
  if (this.reason) {
    throw this.reason;
  }
};

CancelToken.source = function () {
  let cancel;
  const token = new CancelToken(function (c) {
    cancel = c;
  });
  return {
    token: token, // CancelToken
    cancel: cancel // 
  };
};

module.exports = CancelToken;
