const url = require('url');
const http = require('http');
const https = require('https');
const settle = require('../core/settle');
const Cancel = require('../cancel/Cancel');

const isHttps = /https:?/;

/**
 *  XMLHttpRequest
 * @param config
 * @return {Promise<unknown>}
 */
module.exports = function httpAdapter(config) {
  return new Promise((resolvePromise, rejectPromise) => {
    let onCanceled;

    // 
    function done() {
      if (config.cancelToken) {
        config.cancelToken.unsubscribe(onCanceled);
      }
    }

    // resolve
    const resolve = function (value) {
      done();
      resolvePromise(value);
    };

    // reject
    const reject = function (value) {
      done();
      rejectPromise(value);
    };

    const data = config.data;
    const headers = config.headers;
    const headerNames = {}; // header name 

    Object.keys(headers).forEach((name) => {
      headerNames[name.toLowerCase()] = name;
    });

    // 
    const parsed = url.parse(config.url);
    const protocol = parsed.protocol || 'http:';

    // https
    const isHttpsRequest = isHttps.test(protocol);

    const options = {
      hostname: parsed.hostname,
      port: parsed.port,
      path: parsed.path,
      method: config.method.toUpperCase(),
      headers: headers
    };

    // 
    const transport = isHttpsRequest ? https : http;

    // 
    const req = transport.request(options, function handleResponse(res) {
      if (req.aborted) return;

      const stream = res;

      const response = {
        status: res.statusCode,
        statusText: res.statusText,
        headers: res.headers,
        config: config
      };

      const responseBuffer = [];
      let totalResponseBytes = 0;
      // 
      stream.on('data', (chunk) => {
        responseBuffer.push(chunk);
        totalResponseBytes += chunk.length;
      });

      // 
      stream.on('aborted', () => {
        // 
        stream.destroy();

        const error = new Error('Request aborted');
        error.code = 'ECONNABORTED';
        reject(error);
      });

      // 
      stream.on('error', (err) => {
        reject(err);
      });

      // 
      stream.on('end', () => {
        // 
        response.data = responseBuffer.length === 1 ? responseBuffer[0] : Buffer.concat(responseBuffer);
        settle(resolve, reject, response);
      });
    });

    req.on('error', (err) => {
      reject(err);
    });

    // 
    if (config.cancelToken) {
      onCanceled = function (cancel) {
        // 
        if (req.aborted) return;

        // 
        req.abort();
        reject(cancel || new Cancel('canceled'));
      };

      // 
      config.cancelToken.subscribe(onCanceled);
    }

    // 
    req.end(data);
  });
};
